@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Audiowide:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik+One:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-audiowide: Audiowide;
  --font-poppins: Poppins;
  --font-rubik-one: "Rubik One";

  /* font sizes */
  --font-size-mid: 17px;
  --font-size-base-9: 15.9px;
  --font-size-xs: 12px;
  --font-size-lgi-5: 19.5px;
  --font-size-3xs: 10px;
  --font-size-5xl: 24px;
  --font-size-sm-5: 13.5px;
  --font-size-38xl-7: 57.7px;
  --font-size-3xl: 22px;
  --font-size-5xl-7: 24.7px;
  --font-size-xl: 20px;
  --font-size-6xl: 25px;
  --font-size-17xl: 36px;
  --font-size-mini: 15px;
  --font-size-lg: 18px;

  /* Colors */
  --color-gray-100: #1c1c1c;
  --color-gray-200: rgba(255, 255, 255, 0.1);
  --color-white: #fff;
  --color-black: #000;
  --color-yellow: #fcfc03;
  --color-gainsboro: #d9d9d9;
  --color-darkgray: #9e9e9e;

  /* Border radiuses */
  --br-4xs-4: 8.4px;
  --br-9xs: 4px;
  --br-4xs-3: 8.3px;
  --br-xs-2: 11.2px;
  --br-3xs: 10px;
  --br-8xs: 5px;
}
